var sourceType = [
    ['camera'],
    ['album'],
    ['camera', 'album']
]
var sizeType = [
    ['compressed'],
    ['original'],
    ['compressed', 'original']
]
export default {
	components: {
	},
	data() {
		return {
			contents: '',	// 文本内容
			imageList: [],
			sourceTypeIndex: 2,
			sourceType: ['拍照', '相册', '拍照或相册'],
			sizeTypeIndex: 2,
			sizeType: ['压缩', '原图', '压缩或原图'],
			countIndex: 8,
			count: [1, 2, 3, 4, 5, 6, 7, 8, 9]
		}
	},
	// 监听页面卸载
	onUnload() {
	    this.imageList = [],
		this.sourceTypeIndex = 2,
		this.sourceType = ['拍照', '相册', '拍照或相册'],
		this.sizeTypeIndex = 2,
		this.sizeType = ['压缩', '原图', '压缩或原图'],
		this.countIndex = 8;
	},
	onLoad(){
		uni.getLocation({
		    type: 'wgs84',
		    success: function (res) {
		        console.log('当前位置的经度：' + res.longitude);
		        console.log('当前位置的纬度：' + res.latitude);
		    }
		});
	},
	onHide(){
	},
	methods: {
		// 用户发帖--内容和图片, 将该帖子关联该用户
		publish(){
			uni.showLoading({
				title: '加载中'
			});
			let that = this;
			var currentUser = that.Bmob.User.current(); // 当前用户
			var objectId = currentUser.objectId; // 当前用户Id
			// Pointer 类型在数据库是一个json数据类型，只需调用Pointer方法创建一个Pointer对象存入到字段中，如下：
			const pointer = that.Bmob.Pointer('_User')
			const poiID = pointer.set(objectId)
			var query = that.Bmob.Query('postList');
			
			query.set('author',poiID)
			query.set("contents", that.contents)
			query.set("view", 0)
			query.set("likes", 0)
			query.set("comments", 0)
			query.set("images", that.imageList)
			query.save().then(res => {
				uni.hideLoading();
				uni.showModal({
					title: '发表成功',
					content: '是否立即前往查看',
					confirmText: '前往查看',
					cancelText: '继续发帖',
					success: function (res) {
						if (res.confirm) {
							uni.reLaunch({
							    url: '/pages/index/index'
							});
						} else if (res.cancel) {
							
						}
					}
				});
			}).catch(err => {
			  console.log(err)
			})
		},
		sourceTypeChange: function(e) {
		    this.sourceTypeIndex = e.target.value
		},
		sizeTypeChange: function(e) {
		    this.sizeTypeIndex = e.target.value
		},
		countChange: function(e) {
		    this.countIndex = e.target.value;
		},
		chooseImage: async function() {
			let that = this;









		
		    if (this.imageList.length === 9) {
		        let isContinue = await this.isFullImg();
		        console.log("是否继续?", isContinue);
		        if (!isContinue) {
		            return;
		        }
		    }
		    uni.chooseImage({
		        sourceType: sourceType[this.sourceTypeIndex],
		        sizeType: sizeType[this.sizeTypeIndex],
		        count: this.imageList.length + this.count[this.countIndex] > 9 ? 9 - this.imageList.length :
		            this.count[this.countIndex],
		        success: (res) => {
		            this.imageList = this.imageList.concat(res.tempFilePaths);
					var file;
					for (let item of res.tempFilePaths) {
					  console.log('itemn',item)
					  file = that.Bmob.File('abc.jpg', item);
					}
					file.save().then(res => {
					  console.log(res.length);
					  console.log(res);
					})
		        },
		        fail: (err) => {





		        }
		    })
		},
		isFullImg: function() {
		    return new Promise((res) => {
		        uni.showModal({
		            content: "已经有9张图片了,是否清空现有图片？",
		            success: (e) => {
		                if (e.confirm) {
		                    this.imageList = [];
		                    res(true);
		                } else {
		                    res(false)
		                }
		            },
		            fail: () => {
		                res(false)
		            }
		        })
		    })
		},
		previewImage: function(e) {
		    var current = e.target.dataset.src
		    uni.previewImage({
		        current: current,
		        urls: this.imageList
		    })
		},
		async checkPermission(code) {
		    let type = code ? code - 1 : this.sourceTypeIndex;
		    let status = permision.isIOS ? await permision.requestIOS(sourceType[type][0]) :
		        await permision.requestAndroid(type === 0 ? 'android.permission.CAMERA' :
		            'android.permission.READ_EXTERNAL_STORAGE');
		
		    if (status === null || status === 1) {
		        status = 1;
		    } else {
		        uni.showModal({
		            content: "没有开启权限",
		            confirmText: "设置",
		            success: function(res) {
		                if (res.confirm) {
		                    permision.gotoAppSetting();
		                }
		            }
		        })
		    }
		
		    return status;
		}
	}
}
